<template>
	<div id="paotui-index">
		<public-header title="跑腿"></public-header>
		<div class="content">
			<div class="group">
				<van-row gutter="20">
					<van-col span="8" v-if="util.checkperm('errander.order')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/plugin/paotui/list'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-order c-primary"></i>
							</div>
							<div class="bottom">
								<span>订单</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkperm('errander.config')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/plugin/paotui/config'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-settings c-danger"></i>
							</div>
							<div class="bottom">
								<span>设置</span>
							</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {

			}
		},
		components: {
			publicHeader,
		},
		methods: {

		}
	}
</script>

<style>
	#paotui-index .content{
		background-color: #fff;
	}
	#paotui-index .content .group{
		padding: 20px 15px;
	}
	#paotui-index .content .group .van-row{
		text-align: center;
	}
	#paotui-index .content .group .merchant-item{
		margin-bottom: 20px;
	}
	#paotui-index .content .group .merchant-item .top{
		padding: 10px;
	}
	#paotui-index .content .group .merchant-item .top .icon{
		font-size: 40px;
	}
	#paotui-index .content .group .merchant-item .bottom{
		font-size: 14px;
		color: #7D8484;
	}
	#paotui-index .content .group .merchant-item .top .icon-sponsor{
		color: #26A0DF;
	}
	#paotui-index .content .group .merchant-item .top .icon-deletefill{
		color: #5C5C5C;
	}
	#paotui-index .content .group .merchant-item .top .icon-survey{
		color: #F4D81C;
	}
</style>


